
import app from '../app.svg'

export const getGraphData = (current_step, containerWidth, containerHeight) => {

    // 这里可以根据实际情况调整缩放因子
    const scaleFactor = Math.min(containerWidth / 800, containerHeight / 800);

    const nodes1 = [
        {
            id: '1', 
            x: 250 * scaleFactor, 
            y: 150 * scaleFactor, 
            type: 'svg-image-node', 
            label: 'NX Computing Resource Pool',
        },
        {
            id: '2', 
            x: 550 * scaleFactor, 
            y: 150 * scaleFactor, 
            type: 'svg-image-node', 
            label: 'BJ Data Center'
        },

        {
            id: '3', 
            x: 220 * scaleFactor, 
            y: 450 * scaleFactor, 
            type: 'circle',
        },
        {
            id: '4', 
            x: 520 * scaleFactor, 
            y: 450 * scaleFactor, 
            type: 'circle',
        },
    ]

    const nodes2 = [
        {
            id: '1', x: 250 * scaleFactor, y: 150 * scaleFactor, type: 'svg-image-node-green', label: 'NX Computing Resource Pool',
        },
        {
            id: '2', x: 550 * scaleFactor, y: 150 * scaleFactor, type: 'svg-image-node-green', label: 'BJ Data Center'
        },

        {
            id: '3', x: 220 * scaleFactor, y: 450 * scaleFactor, type: 'background-animate',
            color: '#40a9ff',
            size: 20,
        },
        {
            id: '4', x: 520 * scaleFactor, y: 450 * scaleFactor, type: 'background-animate',
            color: '#40a9ff',
            size: 20,
        },
        {
            id: '5', x: 400 * scaleFactor, y: 300 * scaleFactor, type: 'rect', label: 'BJ -> Hebei -> Shanxi -> NX',
            style: {
                fill: 'transparent', // 设置填充颜色为透明
                stroke: 'gray',       // 设置边框颜色为灰色
                lineWidth: 1,         // 设置边框宽度
                width: 380 * scaleFactor,           // 设置矩形宽度
                height: 30 * scaleFactor,            // 设置矩形高度
                lineDash: [2, 2],
            }
        },
    ]

    const nodes3 = [
        {
            id: '1', x: 250 * scaleFactor, y: 150 * scaleFactor, type: 'svg-image-node-green', label: 'NX Computing Resource Pool',
        },
        {
            id: '2', x: 550 * scaleFactor, y: 150 * scaleFactor, type: 'svg-image-node-green', label: 'BJ Data Center'
        },

        {
            id: '3', x: 220 * scaleFactor, y: 450 * scaleFactor, type: 'background-animate',
            color: '#40a9ff',
            size: 20,
        },
        {
            id: '4', x: 520 * scaleFactor, y: 450 * scaleFactor, type: 'background-animate',
            color: '#40a9ff',
            size: 20,
        },
        {
            id: 'app', x: 200 * scaleFactor, y: 100 * scaleFactor,
            size: [40 * scaleFactor, 40 * scaleFactor],
            type: 'inner-animate',
            img: app,
            label: 'App Deployment',
            labelCfg: {
                position: 'right',
            },
        },
        {
            id: '5', x: 400 * scaleFactor, y: 300 * scaleFactor, type: 'rect', label: 'BJ -> Hebei -> Shanxi -> NX',
            style: {
                fill: 'transparent', // 设置填充颜色为透明
                stroke: 'gray',       // 设置边框颜色为灰色
                lineWidth: 1,         // 设置边框宽度
                width: 380 * scaleFactor,           // 设置矩形宽度
                height: 30 * scaleFactor,            // 设置矩形高度
                lineDash: [2, 2],
            }
        },
    ]

    const nodes4 = [
        {
            id: '1', x: 250 * scaleFactor, y: 150 * scaleFactor, type: 'svg-image-node-green', label: 'NX Computing Resource Pool',
        },
        {
            id: '2', x: 550 * scaleFactor, y: 150 * scaleFactor, type: 'svg-image-node-green', label: 'BJ Data Center'
        },
        {
            id: '3', x: 220 * scaleFactor, y: 450 * scaleFactor, type: 'circle',
        },
        {
            id: '4', x: 520 * scaleFactor, y: 450 * scaleFactor, type: 'circle',
        },
        {
            id: '5', x: 400 * scaleFactor, y: 300 * scaleFactor, type: 'rect', label: 'BJ -> Hebei -> Shanxi -> NX',
            style: {
                fill: 'transparent', // 设置填充颜色为透明
                stroke: 'gray',       // 设置边框颜色为灰色
                lineWidth: 1,         // 设置边框宽度
                width: 380 * scaleFactor,           // 设置矩形宽度
                height: 30 * scaleFactor,            // 设置矩形高度
                lineDash: [2, 2],
            }
        },
    ]

    // 节点
    let nodesComm = [
        {
            id: '6', x: 400 * scaleFactor, y: 130 * scaleFactor, type: "ellipse", label: 'Cloud Center',
            labelCfg: {
                fontSize: 16 * scaleFactor,
                fontWeight: 'italic'
            },
            size: [750 * scaleFactor, 200 * scaleFactor],
            style: {
                fill: 'transparent', // 设置填充颜色为透明
                stroke: 'gray',       // 设置边框颜色为灰色
                lineWidth: 1,         // 设置边框宽度
                lineDash: [10, 5, 2, 5],

            }
        },
        {
            id: '7', x: 400 * scaleFactor, y: 450 * scaleFactor, type: 'ellipse', label: 'Private Cloud Network',
            labelCfg: {
                fontSize: 16 * scaleFactor,
                fontWeight: 'italic'
            },
            size: [750 * scaleFactor, 200 * scaleFactor],
            style: {
                fill: 'transparent', // 设置填充颜色为透明
                stroke: 'gray',       // 设置边框颜色为灰色
                lineWidth: 1,         // 设置边框宽度
                lineDash: [10, 5, 2, 5],
            }
        },
    ];

    // 边
    const edges1 = [
        {
            source: '2',
            target: '4',
            type: 'line-dash', // 将边的类型设为二次贝塞尔曲线
            // controlPoints: [{ x: 100, y: 150 }], // 控制点用于控制曲线形状
            style: {
                stroke: '#00FF00', // 设置线条颜色为绿色
                lineWidth: 2,      // 可选：设置线条宽度
                lineDash: [5, 5],  // 使用数组表示虚线模式，[间隔长度, 空白长度]
            },
        },
        {
            source: '4',
            target: '3',
            type: 'line-dash', // 将边的类型设为二次贝塞尔曲线
            controlPoints: [{ x: 275 * scaleFactor, y: 500 * scaleFactor }], // 控制点用于控制曲线形状
            style: {
                stroke: '#00FF00', // 设置线条颜色为绿色
                lineWidth: 2,      // 可选：设置线条宽度
                lineDash: [5, 5],  // 使用数组表示虚线模式，[间隔长度, 空白长度]
            },
        },
        {
            source: '3',
            target: '1',
            type: 'line-dash', // 将边的类型设为二次贝塞尔曲线
            controlPoints: [{ x: 275 * scaleFactor, y: 350 * scaleFactor }], // 控制点用于控制曲线形状
            style: {
                stroke: '#00FF00', // 设置线条颜色为绿色
                lineWidth: 2,      // 可选：设置线条宽度
                lineDash: [5, 5],  // 使用数组表示虚线模式，[间隔长度, 空白长度]
            },
        },
    ];

    // 边
    const edges2 = [
        {
            source: '2',
            target: '4',
            type: 'quadratic', // 将边的类型设为二次贝塞尔曲线
            // controlPoints: [{ x: 100, y: 150 }], // 控制点用于控制曲线形状
            style: {
                stroke: '#00FF00', // 设置线条颜色为绿色
                lineWidth: 2,      // 可选：设置线条宽度
                lineDash: [5, 5],  // 使用数组表示虚线模式，[间隔长度, 空白长度]
            },
        },
        {
            source: '4',
            target: '3',
            type: 'quadratic', // 将边的类型设为二次贝塞尔曲线
            controlPoints: [{ x: 275 * scaleFactor, y: 500 * scaleFactor }], // 控制点用于控制曲线形状
            style: {
                stroke: '#00FF00', // 设置线条颜色为绿色
                lineWidth: 2,      // 可选：设置线条宽度
                lineDash: [5, 5],  // 使用数组表示虚线模式，[间隔长度, 空白长度]
            },
        },
        {
            source: '3',
            target: '1',
            type: 'quadratic', // 将边的类型设为二次贝塞尔曲线
            controlPoints: [{ x: 275 * scaleFactor, y: 350 * scaleFactor }], // 控制点用于控制曲线形状
            style: {
                stroke: '#00FF00', // 设置线条颜色为绿色
                lineWidth: 2,      // 可选：设置线条宽度
                lineDash: [5, 5],  // 使用数组表示虚线模式，[间隔长度, 空白长度]
            },
        },
    ];

    if (current_step === 6) {
        return {
            nodes: nodes1.concat(nodesComm),
        }
    }
    if (current_step === 7) {
        return {
            nodes: nodes2.concat(nodesComm),
        }
    }
    if (current_step === 8) {
        return {
            nodes: nodes2.concat(nodesComm),
            edges: edges1,
        }
    }
    if (current_step === 9) {
        return {
            nodes: nodes3.concat(nodesComm),
            edges: edges1,
        }
    }
    if (current_step === 10) {
        return {
            nodes: nodes4.concat(nodesComm),
            edges: edges2,
        }
    }
}