import React, { useState } from 'react';
import { FloatButton, Row } from 'antd';
import styles from './index.less';
import { CodeOutlined, UserOutlined } from '@ant-design/icons';
import BSSAgentLog from './BSSAgentLog';
import { RESOURCE_AGENT_STATUS_STORAGE_KEY, ResourceAgentStatus } from '@/contexts/constants';
import TaskDetail from '../ResourceAgent/TaskDetail';
import taskDetailStyles from './TaskDetail.less';

const BSSAgent: React.FC = () => {
  const [showSourceLog, setShowSourceLog] = useState(false);

  const resourceReadyStatus = Number(localStorage.getItem(RESOURCE_AGENT_STATUS_STORAGE_KEY));
  
  return (
    <div className={styles.container}>
      {showSourceLog ? (
        <FloatButton style={{ right: 64, bottom: 64 }} icon={<UserOutlined />} onClick={() => setShowSourceLog(false)} />
      ) : (
        <FloatButton style={{ right: 64, bottom: 64 }} icon={<CodeOutlined />} onClick={() => setShowSourceLog(true)} />
      )}
      <h1 className={styles.title}>BSS Agent</h1>
      {showSourceLog && <BSSAgentLog />}
      {!showSourceLog && resourceReadyStatus >= ResourceAgentStatus.resourceSolutionGenerated && (
        <div className={styles.innerContainer}>
          <TaskDetail readyStatus={resourceReadyStatus} setReadyStatus={() => {}} styles={taskDetailStyles} />
        </div>
      )}
    </div>
  );
};

export default BSSAgent; 