import React, { useState } from 'react';
import { Layout, Menu } from 'antd';
import {
  MessageOutlined,
  CloudServerOutlined,
  MonitorOutlined,
  DoubleRightOutlined,
  DoubleLeftOutlined,
  ShoppingOutlined,
} from '@ant-design/icons';
import styles from './index.less';
import AgentsChat from './Chat/chat';
import AssuranceAgent from './AssuranceAgent';
import BSSAgent from './BSSAgent';
import ResourceAgent from './ResourceAgent';

const { Sider, Content } = Layout;

const Admin: React.FC = () => {
  const [selectedMenu, setSelectedMenu] = useState('agents-chat');
  const [collapsed, setCollapsed] = useState(false);

  const menuItems = [
    {
      key: 'agents-chat',
      icon: <MessageOutlined />,
      label: 'Agents Chat',
      onClick: () => setCollapsed(false),
    },
    {
      key: 'bss-agent',
      icon: <ShoppingOutlined />,
      label: 'BSS Agent',
      onClick: () => setCollapsed(true),
    },
    {
      key: 'resource-agent',
      icon: <CloudServerOutlined />,
      label: 'Resource Agent',
      onClick: () => setCollapsed(true),
    },
    {
      key: 'assurance-agent',
      icon: <MonitorOutlined />,
      label: 'Assurance Agent',
      onClick: () => setCollapsed(true),
    },
  ];

  const changeMenu = (menu: string, collapsed: boolean) => {
    setSelectedMenu(menu);
    setCollapsed(collapsed);
  }

  // 根据选中的菜单项渲染对应的内容组件
  const renderContent = () => {
    switch (selectedMenu) {
      case 'agents-chat':
        return <AgentsChat changeMenu={changeMenu} />;
      case 'bss-agent':
        return <BSSAgent />;
      case 'resource-agent':
        return <ResourceAgent changeMenu={changeMenu} />;
      case 'assurance-agent':
        return <AssuranceAgent changeMenu={changeMenu} />;
      default:
        return <AgentsChat changeMenu={changeMenu} />;
    }
  };

  return (
    <Layout className={styles.adminLayout}>
      <Sider 
        width={180} 
        className={styles.sider}
        collapsible
        collapsed={collapsed}
        onCollapse={(value) => setCollapsed(value)}
        trigger={null}
      >
        <Menu
          mode="inline"
          selectedKeys={[selectedMenu]}
          items={menuItems}
          onClick={({ key }) => setSelectedMenu(key)}
          inlineCollapsed={collapsed}
        />
        <div className={styles.siderFooter}>
          {!collapsed && <span className={styles.siderTitle}></span>}
          {React.createElement(collapsed ? DoubleRightOutlined : DoubleLeftOutlined, {
            className: styles.trigger,
            onClick: () => setCollapsed(!collapsed),
          })}
        </div>
      </Sider>
      <Content className={`${styles.content} ${collapsed ? styles.contentExpanded : ''}`}>
        {renderContent()}
      </Content>
    </Layout>
  );
};

export default Admin; 