import React, { useEffect, useState } from 'react';
import { Avatar, Card, FloatButton } from 'antd';
import { RobotOutlined, CloudServerOutlined, MonitorOutlined, CodeOutlined, UserOutlined } from '@ant-design/icons';
import styles from './chat.less';
import '../index.less';
import { useAgentChat, AgentMessage } from '@/contexts/AgentChatContext';
import { RESOURCE_AGENT_STATUS_STORAGE_KEY, RESOURCE_AGENT_STATUS_TIMESTAMP_STORAGE_KEY, ResourceAgentStatus, Scenario, SCENARIO_STORAGE_KEY } from '@/contexts/constants';
import MasterAgentLog from './MasterAgentLog';

const AgentsChat: React.FC<{ changeMenu: (menu: string, collapsed: boolean) => void }> = ({ changeMenu }) => {
  const { messages, loadScenario } = useAgentChat();
  const [currentScenario, setCurrentScenario] = useState(Number(localStorage.getItem(SCENARIO_STORAGE_KEY)));
  const [showSourceLog, setShowSourceLog] = useState(false);

  useEffect(() => {
    scrollToBottom();
  }, [messages]);

  // load scenario
  useEffect(() => {
    if (currentScenario) {
      loadScenario(currentScenario);
    }

    if (currentScenario === Scenario.resourceCompleted) {
      setTimeout(() => {
        localStorage.setItem(SCENARIO_STORAGE_KEY, Scenario.serverCanBeHibernated.toString());
        setCurrentScenario(Scenario.serverCanBeHibernated);
      }, 3000);
    } else if (currentScenario === Scenario.serverHibernated) {
      setTimeout(() => {
        localStorage.setItem(SCENARIO_STORAGE_KEY, Scenario.temperatureAbnormal.toString());
        setCurrentScenario(Scenario.temperatureAbnormal);
      }, 3000);
    } else if (currentScenario === Scenario.temperatureRecovered) {
      setTimeout(() => {
        localStorage.setItem(SCENARIO_STORAGE_KEY, Scenario.bssMetricAbnormal.toString());
        setCurrentScenario(Scenario.bssMetricAbnormal);
      }, 3000);
    }
  }, [loadScenario, currentScenario]);

  // automatically menu change
  useEffect(() => {
    if (currentScenario === Scenario.orderReceived) {
      const resourceStatus = localStorage.getItem(RESOURCE_AGENT_STATUS_STORAGE_KEY);
      if (!resourceStatus || (resourceStatus && Number(resourceStatus) === ResourceAgentStatus.init)) {
        // set resource agent status to bss plan generated
        localStorage.setItem(RESOURCE_AGENT_STATUS_STORAGE_KEY, ResourceAgentStatus.bssPlanGenerated.toString());
        let statusTimestamps = new Map();
        const statusTimestampsStr = localStorage.getItem(RESOURCE_AGENT_STATUS_TIMESTAMP_STORAGE_KEY);
        if (statusTimestampsStr && statusTimestampsStr !== '[]') {
          statusTimestamps = new Map(JSON.parse(statusTimestampsStr));
        }
        statusTimestamps.set(ResourceAgentStatus.bssPlanGenerated.toString(), new Date().toLocaleString());
        localStorage.setItem(RESOURCE_AGENT_STATUS_TIMESTAMP_STORAGE_KEY, JSON.stringify(Array.from(statusTimestamps.entries())));
      }
      // setTimeout(() => {
      //   changeMenu('resource-agent', true);
      // }, 3000);
    } else if (currentScenario === Scenario.temperatureAbnormal) {
      // setTimeout(() => {
      //   changeMenu('assurance-agent', true);
      // }, 3000);
    } else if (currentScenario === Scenario.serverCanBeHibernated) {
      // setTimeout(() => {
      //   changeMenu('assurance-agent', true);
      // }, 3000);
    } else if (currentScenario === Scenario.bssMetricAbnormal) {
      // setTimeout(() => {
      //   changeMenu('assurance-agent', true);
      // }, 3000);
    }
  }, [currentScenario])

  const scrollToBottom = () => {
    const scrollableCard = document.getElementById('scrollableCard');
    if (scrollableCard) {
      scrollableCard.scrollTop = scrollableCard.scrollHeight;
    }
  }

  // 获取 Agent 头像
  const getAgentAvatar = (type: AgentMessage['type']) => {
    switch (type) {
      case 'master':
        return <Avatar icon={<RobotOutlined />} />;
      case 'bss':
        return <Avatar icon={<CloudServerOutlined />} style={{ backgroundColor: "#6a7b8c" }} />;
      case 'resource':
        return <Avatar icon={<CloudServerOutlined />} style={{ backgroundColor: "#6a8c6a" }} />;
      case 'assurance':
        return <Avatar icon={<MonitorOutlined />} style={{ backgroundColor: "#8c6b6a" }} />;
      default:
        return <Avatar icon={<RobotOutlined />} />;
    }
  };

  const getMessageClass = (type: AgentMessage['type']) => {
    switch (type) {
      case 'master':
        return styles.master;
      case 'bss':
        return styles.bss;
      case 'resource':
        return styles.resource;
      case 'assurance':
        return styles.assurance;
      default:
        return '';
    }
  };

  return (
    <Card className={styles.container} id={'scrollableCard'}>
      {showSourceLog ? (
        <FloatButton style={{ right: 64, bottom: 64 }} icon={<UserOutlined />} onClick={() => setShowSourceLog(false)} />
      ) : (
        <FloatButton style={{ right: 64, bottom: 64 }} icon={<CodeOutlined />} onClick={() => setShowSourceLog(true)} />
      )}
      {showSourceLog && <h1 className={styles.title}>Master Agent</h1>}
      {showSourceLog && <MasterAgentLog />}
      {!showSourceLog && (
        <div className={styles.messages}>
          {messages.map((message) => (
            <div
              key={message.id}
              className={`${styles.message} ${getMessageClass(message.type)} ${message.type === 'master' ? styles.master : styles.other}`}
            >
              <div className={styles.avatar}>{getAgentAvatar(message.type)}</div>
              <div className={styles.messageContent}>
                <div className={styles.messageHeader}>
                  <span className={styles.sender}>{message.sender}</span>
                  <span className={styles.timestamp}>{message.timestamp}</span>
                </div>
                <div className={styles.messageText}>{message.content}</div>
              </div>
            </div>
          ))}
        </div>
      )}
    </Card>
  );
};

export default AgentsChat;
