import React, { useState, useEffect } from 'react';
import styles from './index.less';
import { RESOURCE_AGENT_STATUS_STORAGE_KEY, RESOURCE_AGENT_STATUS_TIMESTAMP_STORAGE_KEY, ResourceAgentStatus, Scenario, SCENARIO_STORAGE_KEY } from '@/contexts/constants';
import TaskDetail from './TaskDetail';
import { Tab } from './constant';
import TaskWorkflow from './TaskWorkflow';
import { Button, FloatButton, Table } from 'antd';
import { UserOutlined, CodeOutlined } from '@ant-design/icons';
import ResourceAgentLog from './ResourceAgentLog';

const ResourceAgent: React.FC<{ changeMenu: (menu: string, collapsed: boolean) => void }> = ({ changeMenu }) => {
  const [readyStatus, setReadyStatus] = useState(() => {
    const storedStatus = localStorage.getItem(RESOURCE_AGENT_STATUS_STORAGE_KEY);
    return parseInt(storedStatus || ResourceAgentStatus.init.toString());
  });
  const [tab, setTab] = useState(Tab.TaskList);
  const [showSourceLog, setShowSourceLog] = useState(false);

  useEffect(() => {
    if ( (ResourceAgentStatus.bssPlanGenerated <= readyStatus && readyStatus < ResourceAgentStatus.resourceSolutionGenerated)  && tab === Tab.TaskList) {
      setTimeout(() => {
        setTab(Tab.TaskDetail);
      }, 2000)
    } else if (readyStatus === ResourceAgentStatus.resourceSolutionGenerated && tab === Tab.TaskDetail) {
      // setTimeout(() => {
      //   setTab(Tab.TaskList);
      // }, 3000)
    } else if (readyStatus === ResourceAgentStatus.taskStarted && tab === Tab.TaskList) {
      setTimeout(() => {
        setTab(Tab.TaskWorkflow);
      }, 3000)
    } else if (readyStatus === ResourceAgentStatus.taskCompleted && tab === Tab.TaskWorkflow) {
      // setTimeout(() => {
      //   setTab(Tab.TaskList);
      // }, 3000)
    } else if (readyStatus === ResourceAgentStatus.taskCompleted && tab === Tab.TaskList) {
      if (localStorage.getItem(SCENARIO_STORAGE_KEY) === Scenario.orderReceived.toString()) {
        localStorage.setItem(SCENARIO_STORAGE_KEY, Scenario.resourceCompleted.toString());
        // setTimeout(() => {
        //   changeMenu('agents-chat', false);
        // }, 2000)
      }
    }
  }, [readyStatus, tab]);

  let statusTimestamps = new Map();
  const statusTimestampsStr = localStorage.getItem(RESOURCE_AGENT_STATUS_TIMESTAMP_STORAGE_KEY);
  if (statusTimestampsStr && statusTimestampsStr !== '[]') {
    statusTimestamps = new Map(JSON.parse(statusTimestampsStr));
  }

  const tableData = [
    {
      key: 1,
      no: 1,
      taskId: new Date(statusTimestamps.get(ResourceAgentStatus.bssPlanGenerated.toString())).getTime(),
      taskType: 'create',
      productName: 'AI Medical Model Training',
      taskStatus:
        readyStatus < ResourceAgentStatus.resourceSolutionGenerated ?
          <div className={styles.statusProcessing}>CREATING</div>
          : readyStatus === ResourceAgentStatus.resourceSolutionGenerated
            ? <div className={styles.statusNotStarted}>NOT STARTED</div>
            : readyStatus < ResourceAgentStatus.taskCompleted
              ? <div className={styles.statusProcessing}>PROCESSING</div>
              : <div className={styles.statusCompleted}>COMPLETED</div>,
      creationTime: statusTimestamps.get(ResourceAgentStatus.bssPlanGenerated.toString()) || '',
      completionTime: statusTimestamps.get(ResourceAgentStatus.taskCompleted.toString()) || '',
    },
  ];
  const actionColumn = () => {
    return (
      <>
        <Button type="link" onClick={() => setTab(Tab.TaskDetail)} className={styles.actionButton}>Detail</Button>
        <Button
          type="link"
          onClick={() => {
            setReadyStatus(ResourceAgentStatus.taskStarted);
            localStorage.setItem(RESOURCE_AGENT_STATUS_STORAGE_KEY, ResourceAgentStatus.taskStarted.toString());
            let statusTimestamps = new Map();
            const statusTimestampsStr = localStorage.getItem(RESOURCE_AGENT_STATUS_TIMESTAMP_STORAGE_KEY);
            if (statusTimestampsStr && statusTimestampsStr !== '[]') {
              statusTimestamps = new Map(JSON.parse(statusTimestampsStr));
            }
            statusTimestamps.set(ResourceAgentStatus.taskStarted.toString(), new Date().toLocaleString());
            localStorage.setItem(RESOURCE_AGENT_STATUS_TIMESTAMP_STORAGE_KEY, JSON.stringify(Array.from(statusTimestamps.entries())));
          }}
          disabled={readyStatus !== ResourceAgentStatus.resourceSolutionGenerated}
          className={styles.actionButton}
        >
          Start
        </Button>
        <Button
          type="link"
          onClick={() => setTab(Tab.TaskWorkflow)}
          disabled={readyStatus !== ResourceAgentStatus.taskStarted}
          className={styles.actionButton}
        >
          Workflow
        </Button>
      </>
    );
  };

  return (
    <div className={styles.container}>
      {/* {showSourceLog ? (
        <FloatButton style={{ right: 64, bottom: 64 }} icon={<UserOutlined />} onClick={() => setShowSourceLog(false)} />
      ): (
        <FloatButton style={{ right: 64, bottom: 64 }} icon={<CodeOutlined />} onClick={() => setShowSourceLog(true)} />
      )} */}
      <h1 className={styles.title}>Resource Agent</h1>
      {showSourceLog && <ResourceAgentLog />}
      {!showSourceLog && tab === Tab.TaskList && (
        <>
          <div className={styles.breadcrumb}>
            <span>Task List</span>
          </div>
          <div className={styles.innerContainer}>
            <Table className={styles.table} dataSource={readyStatus >= ResourceAgentStatus.bssPlanGenerated ? tableData : []}>
              <Table.Column title="NO." dataIndex="no" key="no" />
              <Table.Column title="Task ID" dataIndex="taskId" key="taskId" />
              <Table.Column title="Task Type" dataIndex="taskType" key="taskType" />
              <Table.Column title="Product Name" dataIndex="productName" key="productName" />
              <Table.Column title="Task Status" dataIndex="taskStatus" key="taskStatus" />
              <Table.Column title="Creation Time" dataIndex="creationTime" key="creationTime" />
              <Table.Column title="Completion Time" dataIndex="completionTime" key="completionTime" />
              <Table.Column title="Action" key="action" render={() => actionColumn()}
              />
            </Table>
          </div>
        </>
      )}
      {!showSourceLog && tab === Tab.TaskDetail && (
        <>
          <div className={styles.breadcrumb}>
            <a onClick={() => setTab(Tab.TaskList)}>Task List</a>
            <span> \ Task Detail</span>
          </div>
          <div className={styles.innerContainer}>
            <TaskDetail readyStatus={readyStatus} setReadyStatus={setReadyStatus} />
          </div>
        </>
      )}
      {!showSourceLog && tab === Tab.TaskWorkflow && (
        <>
          <div className={styles.breadcrumb}>
            <a onClick={() => setTab(Tab.TaskList)}>Task List</a>
            <span> \ Workflow</span>
          </div>
          <div>
            <TaskWorkflow readyStatus={readyStatus} setReadyStatus={setReadyStatus} />
          </div>
        </>
      )}
    </div>
  );
};

export default ResourceAgent; 