import G6, { Graph } from '@antv/g6'
import ss from '../cloud.svg'
import sgreen from '../cloud-green.svg'
const Util = G6.Util;
export default class FlowGraph {
  public static graph1: Graph
  public static init() {

    const container = document.getElementById('container21');

    function setContainerHeight() {
      // 计算新的高度
      const newHeight = window.innerHeight - 200;
      // 应用到container上
      container.style.height = `${newHeight}px`;
    }

    // 初始化设置高度
    setContainerHeight();

    // 监听窗口大小改变事件
    window.addEventListener('resize', () => {
      setContainerHeight();
    });

    // G6.registerNode('cloud', {
    //   draw(cfg, group) {

    //     // 原始路径数据（未缩放）
    //     const originalPath = [
    //       ['M', 40, 60],
    //       ['C', 20, 30, 60, 20, 80, 40],
    //       ['C', 100, 20, 140, 20, 160, 40],
    //       ['C', 180, 60, 160, 80, 120, 80],
    //       ['C', 100, 100, 60, 100, 40, 80],
    //       ['C', 20, 70, 30, 50, 40, 60],
    //       ['Z']
    //     ];

    //     // 计算关键参数
    //     const originalBBox = {        // 原始包围盒（手动计算或通过 getBBox 获取）
    //       minX: 20, maxX: 180,       // X 范围 20~180 → 宽度 160
    //       minY: 20, maxY: 100        // Y 范围 20~100 → 高度 80
    //     };
    //     const targetSize = [1000, 1000]; // 目标尺寸
    //     const scaleX = targetSize[0] / (originalBBox.maxX - originalBBox.minX); // 1000 / 160 = 6.25
    //     const scaleY = targetSize[1] / (originalBBox.maxY - originalBBox.minY); // 1000 / 80 = 12.5
    //     const centerX = (originalBBox.minX + originalBBox.maxX) / 2; // 原始中心 X: 100
    //     const centerY = (originalBBox.minY + originalBBox.maxY) / 2; // 原始中心 Y: 60

    //     // 构建变换矩阵（关键步骤）
    //     const transform = [
    //       `translate(${-centerX}, ${-centerY})`, // 平移到原点（以原始中心为基准）
    //       `scale(${scaleX}, ${scaleY})`,         // 应用缩放
    //       `translate(${cfg.x}, ${cfg.y})`        // 移动到节点坐标
    //     ].join(' ');


    //     // 计算缩放比例
    //     const shape = group.addShape('path', {
    //       attrs: {
    //         path: originalPath,
    //         stroke: '#ccc',
    //         fill: cfg.color || '#fff',
    //         lineWidth: 2,
    //         // 关键缩放配置
    //         transform: transform
    //       },
    //       name: 'main-shape'
    //     });

    //     // 添加文本标签
    //     if (cfg.label) {
    //       group.addShape('text', {
    //         attrs: {
    //           text: cfg.label,
    //           x: 0,
    //           y: -20,
    //           textAlign: 'center',
    //           fontSize: 14,
    //           fill: '#000',
    //         },
    //         name: 'label-shape',
    //       });
    //     }

    //     return shape;
    //   }
    // });


    // Scale Animation
    G6.registerNode(
      'circle-animate',
      {
        afterDraw(cfg, group) {
          const shape = group.get('children')[0];
          shape.animate(
            (ratio) => {
              const diff = ratio <= 0.5 ? ratio * 10 : (1 - ratio) * 10;
              return {
                r: cfg.size / 2 + diff,
              };
            },
            {
              repeat: true,
              duration: 3000,
              easing: 'easeCubic',
            },
          );
        },
      },
      'circle',
    );

    // Background Animation
    G6.registerNode(
      'background-animate',
      {
        afterDraw(cfg, group) {
          const r = cfg.size / 2;
          const back1 = group.addShape('circle', {
            zIndex: -3,
            attrs: {
              x: 0,
              y: 0,
              r,
              fill: cfg.color,
              opacity: 0.6,
            },
            // must be assigned in G6 3.3 and later versions. it can be any string you want, but should be unique in a custom item type
            name: 'back1-shape',
          });
          const back2 = group.addShape('circle', {
            zIndex: -2,
            attrs: {
              x: 0,
              y: 0,
              r,
              fill: cfg.color,
              opacity: 0.6,
            },
            // must be assigned in G6 3.3 and later versions. it can be any string you want, but should be unique in a custom item type
            name: 'back2-shape',
          });
          const back3 = group.addShape('circle', {
            zIndex: -1,
            attrs: {
              x: 0,
              y: 0,
              r,
              fill: cfg.color,
              opacity: 0.6,
            },
            // must be assigned in G6 3.3 and later versions. it can be any string you want, but should be unique in a custom item type
            name: 'back3-shape',
          });
          group.sort(); // Sort according to the zIndex
          back1.animate(
            {
              // Magnifying and disappearing
              r: r + 10,
              opacity: 0.1,
            },
            {
              duration: 3000,
              easing: 'easeCubic',
              delay: 0,
              repeat: true, // repeat
            },
          ); // no delay
          back2.animate(
            {
              // Magnifying and disappearing
              r: r + 10,
              opacity: 0.1,
            },
            {
              duration: 3000,
              easing: 'easeCubic',
              delay: 1000,
              repeat: true, // repeat
            },
          ); // 1s delay
          back3.animate(
            {
              // Magnifying and disappearing
              r: r + 10,
              opacity: 0.1,
            },
            {
              duration: 3000,
              easing: 'easeCubic',
              delay: 2000,
              repeat: true, // repeat
            },
          ); // 3s delay
        },
      },
      'circle',
    );

    // Image animation
    G6.registerNode(
      'inner-animate',
      {
        afterDraw(cfg, group) {
          const size = cfg.size;
          const width = size[0] - 12;
          const height = size[1] - 12;
          const image = group.addShape('image', {
            attrs: {
              x: -width / 2,
              y: -height / 2,
              width,
              height,
              img: cfg.img,
            },
            // must be assigned in G6 3.3 and later versions. it can be any string you want, but should be unique in a custom item type
            name: 'image-shape',
          });
          image.animate(
            (ratio) => {
              const toMatrix = Util.transform(
                [1, 0, 0, 0, 1, 0, 0, 0, 1],
                [['r', ratio * Math.PI * 2]],
              );
              return {
                matrix: toMatrix,
              };
            },
            {
              repeat: true,
              duration: 3000,
              easing: 'easeCubic',
            },
          );
        },
      },
      'rect',
    );

    // 注册自定义节点
    G6.registerNode('svg-image-node', {
      draw(cfg, group) {
        // 定义基础参数
        const width = cfg.size ? cfg.size[0] : 100;  // 默认宽度
        const height = cfg.size ? cfg.size[1] : 100; // 默认高度

        // 关键：使用本地 SVG 文件路径
        const imageShape = group.addShape('image', {
          attrs: {
            x: -width / 2,       // 中心对齐
            y: -height / 2,
            width: 60,
            height: 60,
            img: ss, // 本地 SVG 路径
          }
        });

        // 添加文字标签（可选）
        group.addShape('text', {
          attrs: {
            text: cfg.label || '',
            fill: '#666',
            x: 0,
            y: height / 2 - 30,
            textAlign: 'center'
          }
        });

        return imageShape;
      }
    });

    G6.registerNode('svg-image-node-green', {
      draw(cfg, group) {
        // 定义基础参数
        const width = cfg.size ? cfg.size[0] : 100;  // 默认宽度
        const height = cfg.size ? cfg.size[1] : 100; // 默认高度

        // 关键：使用本地 SVG 文件路径
        const imageShape = group.addShape('image', {
          attrs: {
            x: -width / 2,       // 中心对齐
            y: -height / 2,
            width: 60,
            height: 60,
            img: sgreen, // 本地 SVG 路径
          }
        });

        // 添加文字标签（可选）
        group.addShape('text', {
          attrs: {
            text: cfg.label || '',
            fill: '#666',
            x: 0,
            y: height / 2 - 30,
            textAlign: 'center'
          }
        });

        return imageShape;
      }
    });

    const lineDash = [4, 2, 1, 2];
    G6.registerEdge(
      'line-dash',
      {
        afterDraw(cfg, group) {
          // get the first shape in the group, it is the edge's path here=
          const shape = group.get('children')[0];
          let index = 0;
          // Define the animation
          shape.animate(
            () => {
              index++;
              if (index > 9) {
                index = 0;
              }
              const res = {
                lineDash,
                lineDashOffset: -index,
              };
              // returns the modified configurations here, lineDash and lineDashOffset here
              return res;
            },
            {
              repeat: true, // whether executes the animation repeatly
              duration: 3000, // the duration for executing once
            },
          );
        },
      },
      'cubic', // extend the built-in edge 'cubic'
    );

    this.graph1 = new Graph({
      container: 'container21',
      width: container.clientWidth,
      height: parseInt(container.clientHeight),
    })

    return this.graph1
  }
}